/*_start_main_css_*/
@import "typography";
@import "../mixin";

html,
body {
	height: 100%; }
#page {
	display: flex;
	flex-direction: column;
	min-height: 100%; }
#page > main {
  flex: 1 0 auto;
  padding-top: 92px;
  @media (max-width: 1300px) {
    padding-top: 88px; }
  @include media-breakpoint-down(lg) {
    padding-top: 72px; } }
#page > footer {
	flex: 0 0 auto; }

body {
  position: relative;
  min-width: 320px;
  font-family: 'Helvetica', sans-serif;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  color: $text-c;
  background-color: $clear;
  overflow-x: hidden; }

/*_ Page_Content _*/
.page-content {
  max-width: 1330px;
  width: calc( 100% - 30px);
  margin-left: auto;
  margin-right: auto;
  background-color: transparent;
  @include media-breakpoint-down(lg) {
    width: calc( 100% - 30px); } }

// Page-Title
.page-title {
  font-size: 48px;
  font-weight: 600;
  line-height: 72px;
  text-align: center;
  margin-bottom: 40px;
  @include media-breakpoint-down(sm) {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 20px; } }

