.how-it-works {
  padding-top: 130px;
  @include media-breakpoint-down(lg) {
    padding-top: 100px; }
  @include media-breakpoint-down(md) {
    padding-top: 80px; }
  @include media-breakpoint-down(sm) {
    padding-top: 50px; }
  // Slider-Nav
  &-slider-nav {
    text-align: center;
    margin-bottom: 16px;
    .prevArrow,
    .nextArrow {
      display: inline-block;
      padding: 16px;
      border-radius: 50%;
      border: $b #CCCCCC;
      cursor: pointer;
      @include media-breakpoint-down(sm) {
        padding: 3px; } }
    .dots {
      display: inline-block;
      margin: 0 30px;
      @include media-breakpoint-down(sm) {
        margin: 0 5px; }
      .slick-dots {
        display: flex;
        flex-wrap: wrap;
        li {
          display: inline-flex;
          align-items: center;
          @include media-breakpoint-down(md) {
            display: none;
            &:first-child,
            &.slick-active,
            &.slick-active + li {
              display: inline-flex; } }
          &::after {
            content: '-------';
            font-size: 17px;
            color: $btn-bg;
            margin: 0 30px;
            @include media-breakpoint-down(md) {
              content: '---';
              margin: 0 15px; }
            @include media-breakpoint-down(sm) {
              margin: 0 10px; } }
          // &:first-child,
          // &:last-child
          //   display: inline-flex
          // &.slick-active,
          // &.slick-active + li,
          // &.slick-active + li + li,
          // &.slick-active + li + li + li
          //   display: inline-flex
          &.slick-active ~ li {
            &::after {
              color: $dark; } }
          &:last-child {
            &::after {
              display: none; } }

          &.slick-active {
            button {
              color: $clear;
              background-color: $btn-bg; } }
          button {
            display: block;
            font-size: 17px;
            font-weight: 500;
            line-height: 50px;
            width: 58px;
            color: $dark;
            border-radius: 50%;
            border: 4px solid $clear;
            box-shadow: 0px 0px 0px 1px darken($block-bg, 10%);
            background-color: $clear;
            padding: 0;
            @include media-breakpoint-down(sm) {
              line-height: 26px;
              width: 30px;
              font-size: 14px;
              border: 2px solid $clear; } } } } } }


  // Company-Block
  .company-block {
    display: inline-block;
    border: $b $block-bord;
    border-radius: $border-radius-block;
    padding: 30px 15px;
    max-width: 375px;
    box-shadow: 0.590633px 0.590633px 1.18127px -0.590633px rgba(204, 206, 215, 0.35), 0px 0px 5.90633px -2.95317px rgba(211, 215, 220, 0.25), 0px 0px 5.90633px -2.95317px rgba(211, 215, 220, 0.5), 0px 2.95317px 2.95317px -0.590633px rgba(197, 202, 209, 0.25), 0px 2.95317px 2.95317px -0.590633px rgba(197, 202, 209, 0.25), 0px 5.90633px 11.8127px -2.36253px rgba(234, 235, 237, 0.25), 0px 5.90633px 11.8127px -2.36253px #EAEBED, inset 1.18127px 1.18127px 1.18127px rgba(255, 255, 255, 0.3), inset 0px 0px 2.36253px #9FA1A8, inset 0px 0px 1.18127px rgba(204, 206, 215, 0.5), inset 0px -2.95317px 5.90633px rgba(255, 255, 255, 0.25), inset 0px -5.90633px 0.590633px rgba(204, 206, 215, 0.4), inset 0px -5.90633px 0.590633px rgba(204, 206, 215, 0.6);
    @include media-breakpoint-down(lg) {
      margin-bottom: 30px; }
    @include media-breakpoint-down(sm) {
      padding: 15px;
      margin-bottom: 15px; }
    .company-img-wrap {
      position: relative;
      width: 120px;
      height: 120px;
      margin: 0 auto 25px;
      @include media-breakpoint-down(sm) {
        width: 80px;
        height: 80px;
        margin-bottom: 15px; }
      .company-img {
        object-fit: cover;
        object-position: center top;
        border-radius: 50%; }
      .company-settings {
        position: absolute;
        bottom: -5px;
        right: -10px;
        transition: .5s;
        &:hover {
          transform: rotate(.2turn); }
        &:focus {
          transform: rotate(.4turn); } } }
    .company-name {
      text-align: center;
      margin-bottom: 10px;
      font-weight: 500;
      @include media-breakpoint-down(sm) {
        margin-bottom: 5px; } }
    .qr-code-wrap {
      width: 110px;
      height: 110px;
      margin: 0 auto 25px;
      @include media-breakpoint-down(sm) {
        margin-bottom: 15px; }
      .qr-code-link {
        display: block;
        pointer-events: none;
        .qr-code-img {
          width: 100%;
          height: auto;
          object-fit: contain; } } }
    .buttons-wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      @include media-breakpoint-down(lg) {
        justify-content: space-evenly; }
      @include media-breakpoint-down(md) {
        justify-content: center; }
      .btn-downl-qr,
      .btn-create-qr {
        border: $b $btn-bord;
        text-align: center;
        width: 100%;
        max-width: 320px;
        padding: 10px 15px;
        border-radius: $border-radius;
        transition: .4s;
        @include media-breakpoint-down(md) {
          max-width: 420px; }
        @include media-breakpoint-down(sm) {
          padding: 5px; } }

      .btn-downl-qr {
        margin-bottom: 10px;
        @include media-breakpoint-down(lg) {
          margin-bottom: 0; }
        @include media-breakpoint-down(md) {
          margin-bottom: 10px; }
        .qr-txt {
          font-weight: 500; } }
      .btn-create-qr {
        font-weight: 500; }
      .btn-icon {
        stroke: currentColor;
        stroke-width: 1.5; }
      .btn-downl-qr:hover,
      .btn-create-qr:hover {
        color: $btn-bord;
        background-color: lighten($block-bg, 2%); }
      .btn-downl-qr:focus,
      .btn-create-qr:focus {
        background-color: $block-bg; } } }

  // Slick-Slide
  .slick-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: grab;
    @include media-breakpoint-down(sm) {
      flex-direction: column; }
    .img-wrap {
      max-width: 480px;
      min-width: 40%;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center; } }
    .text-block {
      max-width: 640px;
      margin-left: 70px;
      @include media-breakpoint-down(md) {
        margin-left: 30px; }
      @include media-breakpoint-down(sm) {
        margin-left: 0; }
      h3 {
        margin-bottom: 18px;
        @include media-breakpoint-down(sm) {
          text-align: center;
          font-size: 16px; } }
      p {
        margin-bottom: 0;
        @include media-breakpoint-down(sm) {
          text-align: center; } } } } }
