.footer {
  background: center top / cover no-repeat url(../img/lending-img/footer.png);
  padding: 100px 0 40px;
  @media (min-width: 1920px) {
    padding: 200px 0 40px; }
  @include media-breakpoint-down(sm) {
    padding: 150px 0 30px; }
  &-title {
    display: block;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    color: $clear;
    text-transform: uppercase;
    margin-bottom: 8px; }
  &-big-link {
    font-size: 32px;
    font-weight: 700;
    line-height: 48px;
    color: $clear;
    text-shadow: 0px 4px 0px $link-txt;
    transition: .3s;
    &:first-of-type {
      font-size: 28px; }
    &:hover {
      color: $clear;
      text-shadow: -4px -4px 0px $link-txt; }
    @include media-breakpoint-down(lg) {
      font-size: 28px; }
    @include media-breakpoint-down(md) {
      display: block;
      margin-bottom: 20px; }
    @include media-breakpoint-down(sm) {
      font-size: 22px; } }
  &-form {
    display: flex;
    align-items: center;
    padding-left: 16px;
    background-color: $clear;
    border-radius: $border-radius;
    overflow: hidden;
    max-width: 490px;
    @include media-breakpoint-down(md) {
      margin-bottom: 20px;
      padding-left: 10px; }
    @include media-breakpoint-down(sm) {
      flex-wrap: wrap;
      padding-left: 0; }
    svg {
      min-width: 20px;
      @include media-breakpoint-down(sm) {
        margin-left: 10px; } }
    [type=email] {
      padding: 12px;
      flex-basis: 250px;
      @include media-breakpoint-down(lg) {
        flex-basis: 200px; }
      @include media-breakpoint-down(sm) {
        flex-basis: auto; } }
    [type=submit] {
      flex: auto;
      background-color: $dark;
      color: $clear;
      padding: 12px;
      text-align: center;
      transition: .5s;
      &:hover {
        box-shadow: inset 210px 0px $link-txt;
        @include media-breakpoint-down(sm) {
          box-shadow: inset 350px 0px $link-txt; } } } }
  .links-wrap {
    display: flex;
    .social-links {
      border-radius: $border-radius;
      margin-right: 12px;
      transition: .3s;
      &:hover {
        box-shadow: -5px 5px 5px -3px $link-txt;
        transform: rotateY(10deg) rotateX(10deg); }
      &:active {
        transform: rotate(0) scale(1.1); } } }

  .footer-bottom-text {
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    color: $clear;
    padding: 40px 0 0;
    @include media-breakpoint-down(sm) {
      font-size: 11px; } } }
