.advantages {
  padding-top: 90px;
  @include media-breakpoint-down(lg) {
    padding-top: 80px; }
  @include media-breakpoint-down(md) {
    padding-top: 60px; }
  @include media-breakpoint-down(sm) {
    padding-top: 40px; }
  &-slider {
    .slick-track {
      display: flex; } }
  &-block {
    padding: 35px 18px;
    background-color: $lend-block-bg;
    margin-right: 4px;
    height: auto;
    min-height: 465px;
    border-radius: $border-radius-block + 4px;
    @include media-breakpoint-down(md) {
      min-height: 350px;
      padding: 25px 18px; }
    @include media-breakpoint-down(sm) {
      min-height: 300px; }

    h4 {
      min-height: calc( 20% - 20px );
      @include media-breakpoint-down(md) {
        min-height: calc( 30% - 20px ); }
      @include media-breakpoint-down(sm) {
        min-height: calc( 25% - 20px ); } }
    .img-wrap {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      min-height: 265px;
      @include media-breakpoint-down(md) {
        align-items: center;
        min-height: 200px; }
      @include media-breakpoint-down(sm) {
        min-height: auto;
        max-height: 180px;
        overflow: hidden; }
      img {
        width: 100%;
        height: 100%;
        max-height: 265px;
        object-fit: contain;
        object-position: center bottom;
        @include media-breakpoint-down(md) {
          max-height: 200px; }
        @include media-breakpoint-down(sm) {
          max-height: 180px; } } } }
  .slick-dots {
    display: flex;
    justify-content: center;
    li {
      background-color: $btn-bg;
      border-radius: 50%;
      font-size: 0;
      padding: 5px;
      margin: 30px 3px 0;
      transition: .3s;
      &.slick-active {
        transform: scale(1.2);
        button {
          background-color: $clear; } }
      button {
        display: block;
        padding: 0;
        width: 3px;
        height: 3px;
        border-radius: 50%; } } } }
