.placement-xamples {
  padding-top: 100px;
  background-image: linear-gradient(180deg, #F5F5FF 41.55%, rgba(245, 245, 255, 0) 100%);
  margin-bottom: 60px;
  @include media-breakpoint-down(lg) {
    padding-top: 50px; }
  @include media-breakpoint-down(sm) {
    padding-top: 30px;
    margin-bottom: 20px; }
  .sub-title {
    max-width: 755px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-size: 17px;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 45px; }
  .nav-tabs {
    display: flex;
    margin-bottom: 35px;
    @include media-breakpoint-down(lg) {
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 15px; }
    @include media-breakpoint-down(sm) {
      margin-bottom: 5px; }
    .nav-item {
      padding: 0;
      flex: auto;
      text-align: center;
      @include media-breakpoint-down(lg) {
        margin-bottom: 15px; }
      @include media-breakpoint-down(sm) {
        margin-bottom: 5px; }
      .nav-link {
        display: block;
        font-size: 15px;
        font-weight: 500;
        line-height: 24px;
        padding: 12px 34px;
        margin-right: 8px;
        border-radius: $border-radius;
        // white-space: nowrap
        background-color: $clear;
        background-image: linear-gradient(90deg, $btn-bg,$btn-bg);
        background-size: 1% 0%;
        background-repeat: repeat-x;
        background-position: 0 100%;
        transition: .3s;
        &.active {
          background-size: 1% 100%;
          color: $clear; }
        @media (max-width: 1300px) {
          padding: 12px; }
        @include media-breakpoint-down(lg) {
          font-size: 14px; }
        @include media-breakpoint-down(sm) {
          padding: 5px 8px;
          font-size: 12px;
          margin-right: 5px; } } } }

  .tab-content {
    position: relative;
    height: fit-content;
    padding-top: 35px;
    @include media-breakpoint-down(lg) {
      padding-top: 0; }
    .tab-pane {
      // display: none
      // position: absolute
      display: block;
      max-height: 0;
      overflow: hidden;
      animation: tabHide .5s forwards; }
    .tab-pane.active,
    .tab-pane.show {
      animation: tabShow .5s forwards .25s; }

    .small-size-areas {
      .img-wrap {
        overflow: hidden;
        border-radius: $border-radius-reviews;
        max-width: 300px;
        max-height: 250px;
        padding: 0 15px 0 0;
        margin-bottom: 12px;
        @media (max-width: 1300px) {
          margin: 0 auto 12px; }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center; } }
      .text {
        text-transform: uppercase;
        font-size: 17px;
        line-height: 24px;
        @media (max-width: 1300px) {
          display: block;
          max-width: 285px;
          margin: 0 auto; } } }
    .slick-dots {
      display: flex;
      justify-content: center;
      margin-bottom: 10px;
      li {
        background-color: $btn-bg;
        border-radius: 50%;
        font-size: 0;
        padding: 5px;
        margin: 30px 3px 0;
        transition: .3s;
        &.slick-active {
          transform: scale(1.2);
          button {
            background-color: $clear; } }
        button {
          display: block;
          padding: 0;
          width: 3px;
          height: 3px;
          border-radius: 50%; } } } } }
@keyframes tabShow {
  0% {
    max-height: 0;
    opacity: 0; }
  100% {
    max-height: 3000px;
    opacity: 1; } }

@keyframes tabHide {
  0% {
    opacity: 0;
    max-height: 3000px; }
  100% {
    max-height: 0;
    opacity: 0; } }

