.first-screen-wrap {
  background-image: linear-gradient(180deg, #E6EAF5 0%, rgba(230, 234, 245, 0) 100%);
  .first-screen {
    .text-wrap {
      padding-top: 120px;
      padding-bottom: 150px;
      @include media-breakpoint-down(sm) {
        padding-top: 30px;
        padding-bottom: 30px; }
      h1 {
        font-size: 48px;
        font-weight: 600;
        line-height: 48px;
        margin-bottom: 24px;
        @include media-breakpoint-down(lg) {
          font-size: 40px; }
        @include media-breakpoint-down(md) {
          font-size: 34px; } }
      .sub-title {
        font-size: 24px;
        line-height: 32px;
        margin-bottom: 30px;
        @include media-breakpoint-down(lg) {
          font-size: 22px; }
        @include media-breakpoint-down(md) {
          font-size: 20px; } }
      .site-btn {
        min-width: 264px;
        @include media-breakpoint-down(sm) {
          display: block;
          width: fit-content;
          margin: 0 auto; } } }

    .img-wrap {
      margin-top: 25px;
      max-width: 578px;
      max-height: 578px;
      @include media-breakpoint-down(sm) {
        max-height: 400px;
        max-width: 400px; }
      img {
        height: 100%;
        width: 100%;
        object-fit: contain;
        object-position: center;
        @include media-breakpoint-down(sm); } } } }
