@import "../mixin";

/* icons */
@import "../icons";

.rate-pay-form {
  -moz-user-select: none;
  -o-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }

h1,h2,h3,h4,h5 {
  color: $title-c;
  // font-family: $title
  font-weight: 500;
  font-style: normal; }
.page-title {
  margin-bottom: 80px;
  @include media-breakpoint-down(md) {
    margin-bottom: 60px; }
  @include media-breakpoint-down(sm) {
    margin-bottom: 30px; } }
h1, .h1 {
  // color: $clear
  font-size: 34px;
  line-height: 44px;
  font-weight: 600;
  @include media-breakpoint-down(sm) {
    font-size: 28px;
    line-height: 38px; }
  // @include media-breakpoint-down(lg)
  //   font-size: 32px
  //   line-height: 42px
  // @include media-breakpoint-down(md)
  //   font-size: 30px
 }  //   line-height: 40px
h2, .h2 {
  font-size: 28px;
  line-height: 36px;
  font-weight: 500;
  margin-bottom: 30px;
  @include media-breakpoint-down(sm) {
    font-size: 24px;
    line-height: 30px; } }

h3, .h3 {
  font-size: 22px;
  line-height: 28px;
  font-weight: 500;
  margin-bottom: 30px;
  @include media-breakpoint-down(md) {
    font-size: 20px; }
  @include media-breakpoint-down(sm) {
    font-size: 18px;
    line-height: 24px; } }

h4, .h4 {
  font-size: 20px;
  line-height: 20px;
  margin-bottom: 20px;
  @include media-breakpoint-down(md) {
    font-size: 18px; }
  @include media-breakpoint-down(sm) {
    font-size: 16px; } }

h5, .h5 {
  font-size: 20px;
  line-height: 28px;
  margin-bottom: 16px;
  // @include media-breakpoint-down(md)
  //// font-size: 18px
  //// line-height: 26px
  //// margin-bottom: 13px
  // @include media-breakpoint-down(sm)
  //// line-height: 24px
 }  //// margin-bottom: 10px

p {
  // font-family: $text
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  margin-bottom: 15px;
  @include media-breakpoint-down(sm) {
    font-size: 14px;
    line-height: 20px; }
  // @include media-breakpoint-down(md)
  //   font-size: 16px
 }  //   line-height: 30px

input,
textarea {
  border: none;
  outline: none;
  font-size: 16px;
  &::placeholder {
    color: $plaseh-c;
    transition: .4s; }
  &:focus {
    &::placeholder {
      font-size: 0;
      color: transparent; } } }
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 50px $block-bg inset;
  -webkit-text-fill-color: $dark; }
input:-webkit-autofill::first-line {
  font-size: 16px;
  -webkit-font-size: 16px;
  line-height: 24px;
  font-family: inherit;
 }  // font-weight: 400

button,
[type=submit] {
  border: none;
  background-color: transparent; }

a {
  // font-family: $text
  text-decoration: none;
  color: $text-c;
  &:hover {
    text-decoration: none;
    color: $text-c; } }

// hard disabled links
[href='#none'] {
 }  // pointer-events: none

