.rate-pay-block {
  max-width: 456px;
  background-color: $site-bg;
  border-radius: $border-radius;
  overflow-x: hidden;
  margin: 0 auto;
  @include media-breakpoint-down(sm) {
      margin-bottom: 15px; }
  .rate-body {
    padding: 24px 32px;
    border-bottom: $b $block-bord;
    @include media-breakpoint-down(sm) {
      padding: 24px 15px; }
    .rate-name {
      display: inline-flex;
      font-size: 20px;
      font-weight: 500;
      line-height: 16px;
      background-color: $standart-title-bg;
      border-radius: $border-radius-block;
      padding: 8px 24px 10px;
      margin: 15px; }
    .rate-price,
    .date-finish {
      display: inline-block;
      max-width: 50%;
      @include media-breakpoint-down(sm) {
        max-width: none; } }

    .rate-price {
      margin-right: 15px;
      max-width:  50%;
      @include media-breakpoint-down(sm) {
        margin-right: 20px; }
      .price-number {
        font-size: 36px;
        font-weight: 700;
        @include media-breakpoint-down(sm) {
          font-size: 26px; } }
      .period {
        font-size: 24px;
        font-weight: 700;
        @include media-breakpoint-down(sm) {
          font-size: 20px; } }
      .amount {
        display: block;
        font-size: 16px; } }
    .date-finish {
      vertical-align: top;
      .date-finish-txt {
        @include media-breakpoint-down(sm) {
          font-size: 14px; }
        time {
          display: flex;
          align-items: center;
          .icon-time {
            margin-right: 10px;
            @include media-breakpoint-down(sm) {
              width: 21px;
              height: 21px;
              margin-right: 8px;
              transition: .3s; } } } } }
    .rate-options {
      padding-top: 25px;
      padding-left: 40px;
      @include media-breakpoint-down(sm) {
        padding-top: 15px; }
      &-item {
        position: relative;
        margin-bottom: 10px;
        font-size: 15px;
        line-height: 20px;
        padding: 0;
        &::before {
          content: '';
          display: inline-block;
          position: absolute;
          left: -40px;
          top: 50%;
          transform: translateY(-50%);
          height: 28px;
          width: 28px;
          min-width: 28px;
          background-color: $circle-check;
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.0772 0.512379L5.45731 9.13186L1.97286 5.64816C1.54467 5.22047 0.851022 5.22022 0.422831 5.64841C-0.00552704 6.07677 -0.00552704 6.7705 0.422831 7.19869L4.68242 11.4576C4.89648 11.6714 5.1769 11.7784 5.45731 11.7784C5.73773 11.7784 6.0189 11.6714 6.23287 11.4573C6.23354 11.4564 6.23404 11.4554 6.23513 11.4544L15.6272 2.06241C16.0555 1.63447 16.0555 0.940235 15.6272 0.512295C15.1991 0.0841036 14.5049 0.0841042 14.0772 0.512379Z' fill='#{$svg-check}'/%3E%3C/svg%3E");
          background-repeat: no-repeat;
          background-position: center;
          border-radius: 50%;
          margin-right: 12px; }
        .qr-txt {
          font-weight: 500;
          white-space: nowrap; } } } } }
.rate-pay-form,
.rate-pay-footer {
  padding: 24px 15px 24px 32px;
  @include media-breakpoint-down(md) {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    padding: 24px 15px; }
  .quantity-wrap {
    display: inline-block;
    .quantity {
      @include media-breakpoint-down(md) {
        margin-right: 15px; } }
    .quantity-title {
      display: block;
      margin-bottom: 10px; } }
  .btn-rate-pay {
    @include media-breakpoint-down(md) {
      padding: 10px 20px;
      flex: auto; } } }

// Demo
.rate-pay-block-demo {
  .rate-body {
    .rate-name {
      background-color: $standart-rate-bg; }
    .rate-price {
      // margin-right: 30px
      .price-number {
        font-weight: 500;
        font-size: 24px; }
      .period {
        font-size: 16px;
        font-weight: 500;
        padding-left: 10px;
        @include media-breakpoint-down(md) {
          padding-left: 0; } } } }
  .footer-text {
    padding: 8px;
    margin-bottom: 8px;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    color: $text-c;
    background-color: $block-bg;
    border-radius: $border-radius-lang-wr;
    flex: auto;
    svg {
      min-width: 24px;
      margin-right: 12px; } }

  .btn-rate-pay {
    width: 100%; } }
